<script>
    import { filters, studies } from '../store';

    import Study from '../Study.svelte';

    import { STUDIES_CATS } from '../content/studies';
    import { flip } from 'svelte/animate';

    let animating = false;
    const DURATION = 300;

    function handleFilter(cat) {
        if(animating) return;

        if($filters.includes(cat)){
            $filters = $filters.filter( (f) => {
                return f !== cat;
            } )
        }else {
            $filters = [...$filters, cat];
        }
    }

    function clickBlock() {
        animating = true;
        
        setTimeout(() => animating = false, DURATION);
    }

    function checkFilters($filters){
        clickBlock();
        let tempStudies = $studies;

        tempStudies.map( s => {
            if($filters.length === 0) return s.show = true;
            let hasOne = false;

            for(let i=0; i<s.techs.length; i++) {
                if($filters.includes(s.techs[i])) {
                    hasOne = true;
                    break;
                }
            }

            if(hasOne) return s.show = true;
            return s.show = false;
        })

        $studies = tempStudies;
    }

    $: checkFilters($filters);
</script>

<div class="container">
    <h2><span class="font-light">My</span> Studies</h2>
    <div class="filter-nav">
        <h4>
            { #if $filters.length === 0 }
                Filter:
            { :else }
                <span on:click="{ () => $filters = [] }">Clear filters</span>
            { /if }
        </h4>
        <ul class="filter">
            { #each STUDIES_CATS as cat }
                <li on:click="{ () => handleFilter(cat) }" data-filter="{cat}" class="{ $filters.includes(cat) ? 'active' : '' }">{ cat }</li>
            { /each }
        </ul>
    </div>
    <div class="studies">
        { #each $studies.filter( s => s.show == true) as study (study.id) }
            <div class="study-container" animate:flip="{{ duration: DURATION }}">
                <Study {study} />
            </div>
        { /each }
    </div>
</div>

